import React from 'react'
import ImageOne from "../../images/login.svg"

const Discover = () => {
    return (
        <section className="centerFlex flex-col lg:flex-row
        p-5 h-screen  bg-gray-100 sm:gap-8 " id="discover">
            <div className="flex justify-center items-center mt-8 sm:m-0 text-center md:items-start md:text-left flex-col row-start-1 lg:row-start-auto gap-1 p-8">
                <span className=" underlinetxt">Digital bank</span>
                <h2 className="text-black ">Unlimited Transactions with zero fees</h2>
                <p>Get acces to our exclusive app that allows you to send unlimited transactions without any fees</p>
                <div className="flex sm:flex-row mt-4">
                    <button className="btn1">Learn More</button>
                </div>
            </div>
            <div className="flex items-center justify-center  lg:justify-start">
             <img src={ImageOne} alt="" className="h-96 w-96 lg:h-4/5 lg:w-4/5"/>
            </div>
            {/* right */}
        </section>
    )
}

export default Discover
